.flip-up {
  -webkit-transform: rotateX(180deg);
          transform: rotateX(180deg); }

.light-switch {
  padding-top: 3%; }

.block1 {
  color: white;
  text-align: center;
  position: relative;
  bottom: 30px; }

#media-monster {
  height: 50vw; }

.invisible-monster {
  display: none; }

.monster-layer {
  pointer-events: all;
  position: absolute;
  left: 60%;
  top: 75%;
  -webkit-animation: flicker var(--animation-time) ease-in-out alternate infinite;
  animation: flicker var(--animation-time) ease-in-out alternate infinite; }

.monster-layer:hover {
  -webkit-animation: vanish 10s ease-in-out infinite;
  animation: vanish 10s ease-in-out infinite; }

.smoke-layer {
  position: absolute;
  left: 60%;
  top: 75%; }

@keyframes flicker {
  0%, 19.999%, 22%, 62.999%, 64%, 68.999%, 70%, 100% {
    opacity: .99;
    text-shadow: -1px #000; }
  20%, 21.999%, 63%, 63.999%, 69%, 69.999% {
    opacity: 0;
    text-shadow: none; } }
@-webkit-keyframes flicker {
  0%, 19.999%, 22%, 62.999%, 64%, 64.999%, 70%, 100% {
    opacity: .99;
    text-shadow: -1px #000; }
  20%, 21.999%, 63%, 63.999%, 65%, 69.999% {
    opacity: 0.7;
    text-shadow: none; } }
@keyframes vanish {
  0% {
    opacity: 0;
    text-shadow: none; }
  100% {
    opacity: 0;
    text-shadow: none; } }
@-webkit-keyframes vanish {
  0% {
    opacity: 0;
    text-shadow: none; }
  100% {
    opacity: 0;
    text-shadow: none; } }
@media only screen and (max-width: 500px) {
  .monster-layer {
    width: 20px;
    top: 70vw; } }



